import React from 'react';
import { connect } from 'react-redux';
import { withTranslation } from 'react-i18next';
import { t } from '../../../i18n';
import {
  getSelectedAccountRIB,
  getSelectedAccountNumber,
} from '../../../redux/accounts/accounts.selectors';
import { exportRibToPdf } from '../../../utils/exportUtils';
import RibTemplate from '../../../pdf/Templates/RibTemplate';

class RIB extends React.PureComponent {
  render() {
    const {
      bankCode,
      agencyCode,
      accountNumber,
      ribKey,
      iban,
    } = this.props.rib;
    const banks = [
      { bankId: 1, bankAbreviation: 'BACI' },
      { bankId: 2, bankAbreviation: 'BABN' },
      { bankId: 3, bankAbreviation: 'BATG' },
      { bankId: 4, bankAbreviation: 'BABF' },
      { bankId: 5, bankAbreviation: 'BANE' },
      { bankId: 6, bankAbreviation: 'BAML' },
      { bankId: 7, bankAbreviation: 'BASN' },
      { bankId: 9, bankAbreviation: 'BIAN' },
      { bankId: 10, bankAbreviation: 'BAGB' },
    ];

    const bankAbreviation =
      banks.filter(bank => bank.bankId === this.props.bankId).length > 0
        ? banks.filter(bank => bank.bankId === this.props.bankId)[0]
            .bankAbreviation
        : '';
    return (
      <>
        <RibTemplate
          rib={this.props.rib}
          domiciliation={this.props.domiciliation}
          fullName={this.props.fullName}
          bankAbreviation={bankAbreviation}
          agency={this.props.agency}
        />
        <div className="page-container_inner">
          <div className="main-content container-fluid">
            <div className="row mx-auto">
              <div className="col-sm-12">
                <div className="panel panel-default">
                  <div className="panel-heading panel-heading-divider">
                    <div className="row">
                      <div className="col-md-6">{t('Accounts:rib')}</div>
                      <div className="col-md-6">
                        <div className="download_links dropdown_area float_right">
                          <span className="purple_download">
                            {t('Accounts:download')} :&nbsp;
                            <button
                              onClick={() =>
                                exportRibToPdf(
                                  `RIB_${this.props.selectedAccountNumber}`,
                                )
                              }
                            >
                              {t('Accounts:pdfButton')}
                            </button>
                          </span>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="panel-body rib_page">
                    <div className="rib_content" id="RIB">
                      <div className="releve_bancaire">
                        <h3>{t('Accounts:ribTitle')}</h3>
                      </div>
                      <div className="rib_top">
                        <div className="left_rib_top">
                          <table>
                            <thead>
                              <tr>
                                <th width="23%">{t('Accounts:bankCode')}</th>
                                <th width="15%">{t('Accounts:agencyCode')}</th>
                                <th>{t('Accounts:accountNumber')}</th>
                                <th width="12%" align="center">
                                  {t('Accounts:ribKey')}
                                </th>
                              </tr>
                            </thead>
                            <tbody>
                              <tr>
                                <td>{bankCode}</td>
                                <td>{agencyCode}</td>
                                <td>{accountNumber}</td>
                                <td>{ribKey}</td>
                              </tr>
                              <tr>
                                <td colSpan="4">&nbsp;</td>
                              </tr>
                            </tbody>
                            <tfoot>
                              <tr>
                                <td colSpan="2">{t('Accounts:iban')}</td>
                                <td>{iban}</td>
                              </tr>
                            </tfoot>
                          </table>
                        </div>
                        <div className="right_rib_top">
                          <h3>{t('Accounts:agency')} </h3>
                          <p>{this.props.agency}</p>
                        </div>
                      </div>
                      <div className="rib_bot">
                        <div className="left_rib_bot">
                          <h3>{t('Accounts:domiciliation')}</h3>
                          <p>{this.props.domiciliation}</p>
                        </div>
                        <div className="right_rib_bot">
                          <h3>{t('Accounts:accountOwner')}</h3>
                          <p>{this.props.fullName}</p>
                        </div>
                      </div>
                      <div className="rib_remarque">
                        <p>{t('Accounts:ribRemark')}</p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

const mapStateToProps = state => ({
  selectedAccountNumber: getSelectedAccountNumber(state),
  rib: getSelectedAccountRIB(state),
  fullName: state.Accounts.signaletic.fullName,
  bankId: state.Accounts.signaletic.branch.bank,
  agency: state.Accounts.signaletic.branch.name,
  domiciliation: state.Accounts.signaletic.branch.address,
});

export default withTranslation()(
  connect(
    mapStateToProps,
    null,
  )(RIB),
);
