import { actionCreator } from '../../utils/generators';
import OperationsActionTypes from './operations.types';

export const getOperations = () => ({
  type: actionCreator('req', OperationsActionTypes.GET_OPERATIONS),
});
export const getAllAccountsOperations = () => ({
  type: actionCreator('req', OperationsActionTypes.GET_ALL_ACCOUNTS_OPERATIONS),
});
export const setPickedAccount = accountId => ({
  type: actionCreator('req', OperationsActionTypes.SET_PICKED_ACCOUNT),
  payload: accountId,
});
