import React from 'react';
import { withTranslation } from 'react-i18next';
import { t } from '../../i18n';
import Logo from '../../components/Logo/PNGLogo';
import { pdfStyles } from './PdfTemplate.styles';

const RibTemplate = props => (
  <div id="pdf" style={pdfStyles.pdf}>
    <table id="rib-header">
      <tbody>
        <tr>
          <td width="">
            <Logo />
          </td>
          <td>
            <b>{t('Accounts:ribTitle')}</b>
          </td>
          <td>
            <h2>{t('Accounts:accountTitle')}</h2>
            <br />
            {props.fullName}
            <br />
            {`${t('Accounts:agency')} :`}
            <br />
            {props.agency}
            <br />
            {props.domiciliation}
          </td>
        </tr>
      </tbody>
    </table>
    <table id="reminder">
      <tbody>
        <tr>
          <td width="120">{t('Accounts:ribRemark')}</td>
        </tr>
      </tbody>
    </table>
    <table id="rib-table">
      <thead>
        <tr>
          <th>{t('Accounts:bankCode')}</th>
          <th>{t('Accounts:agencyCode')}</th>
          <th>{t('Accounts:accountNumber')}</th>
          <th>{t('Accounts:ribKey')}</th>
          <th>{t('Accounts:domiciliation')}</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>{props.rib.bankCode}</td>
          <td>{props.rib.agencyCode}</td>
          <td>{props.rib.accountNumber}</td>
          <td>{props.rib.ribKey}</td>
          <td>{props.bankAbreviation}</td>
        </tr>
      </tbody>
    </table>
    <table id="iban-table">
      <thead>
        <tr>
          <th>{t('Accounts:iban')}</th>
          <th>{t('Accounts:swift')}</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>{props.rib.iban}</td>
          <td>XXXXXXXXXX</td>
        </tr>
      </tbody>
    </table>
  </div>
);
export default withTranslation()(RibTemplate);
